@import url("https://fonts.googleapis.com/css2?family=Great+Vibes&family=Montserrat&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  scroll-snap-type: proximity;
}
:root {
  --background-color: white;
  --text-color: black;
  --button-color: #6509ef;
  --textbutton-color: white;
  --header-font: "Great Vibes", cursive;
  --body-font: "Montserrat", sans-serif;
}
body {
  font-size: 62.5%;
}
.get-started-title {
  text-align: center;
  font-size: 5rem;
  font-family: var(--body-font);
  margin-top: 15rem;
}
.get-started-title span {
  font-family: var(--header-font);
  color: var(--button-color);
  font-weight: bolder;
}
.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 1rem;
}

.left-column {
  margin-top: 5rem;
  text-align: justify;
}

.left-column h2 {
  font-size: 2rem;
  font-family: var(--body-font);
}

.left-column p {
  margin-top: 2rem;
  font-size: 1rem;
  font-family: var(--body-font);
  color: var(--text-color);
  font-weight: bold;
}
.left-column ul {
  margin-top: 2rem;
  font-size: 1rem;
  font-family: var(--body-font);
  color: var(--text-color);
  font-weight: bold;
}

.left-column li {
  margin-bottom: 1rem;
  list-style-type: decimal;
}
.right-column {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

form {
  width: 100%;
  max-width: 750px;
  padding: 2rem 2rem;
  background-color: #f2f2f2;
  border-radius: 1rem;
}

h3 {
  margin-bottom: 3rem;
  font-size: 2rem;
  font-family: var(--body-font);
}

.form-group {
  margin-bottom: 1.5rem;
}

label {
  display: block;
  font-weight: bold;
  font-size: 1rem;
  font-family: var(--body-font);
  margin-bottom: 0.5rem;
}

input[type="text"],
input[type="tel"] {
  width: 100%;
  padding: 1rem 2rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-family: var(--body-font);
}

button[type="submit"] {
  background-color: #6e1eff;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 1.2rem 2rem;
  font-family: var(--body-font);
  cursor: pointer;
  font-weight: bold;
  font-size: 1rem;
  width: 100%;
}

button[type="submit"]:hover {
  background-color: #520ee8;
}

@media screen and (max-width: 950px) {
  .get-started-title {
    font-size: 2rem;
  }
}

@media (max-width: 768px) {
  .grid-container {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}
