@import url("https://fonts.googleapis.com/css2?family=Great+Vibes&family=Montserrat&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --background-color: white;
  --text-color: black;
  --button-color: #6509ef;
  --textbutton-color: white;
  --header-font: "Great Vibes", cursive;
  --body-font: "Montserrat", sans-serif;
}
body {
  font-size: 62.5%;
}
.testimonial-slider {
  margin: 4rem auto;
  text-align: center;
  padding: 1rem;

  position: relative;
}

.testimonial {
  border: 3px solid black;
  margin-bottom: 1rem;
  padding: 5rem 2rem;
  background-color: #e0cefc;
  border-radius: 1rem;
}
.testimonial-text {
  font-size: 1.5rem;
  font-family: var(--body-font);
  margin-bottom: 2rem;
  font-weight: bold;
}

.testimonial-author {
  font-size: 1rem;
  font-style: italic;
  font-family: var(--body-font);
  font-weight: bold;
}

.testimonial-controls {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.prev-btn,
.next-btn {
  position: absolute;
  border: none;
  background: #6e1eff;
  color: white;
  padding: 1rem 5rem;
  margin: 0 0.5rem;
  border-radius: 4rem;
  font-size: 1.2rem;
  font-family: var(--body-font);
  cursor: pointer;
}

.prev-btn:hover,
.next-btn:hover {
  background: #4c00b2;
}

@media (max-width: 768px) {
  .testimonial-slider {
    max-width: 100%;
  }
  .prev-btn {
    top: 110%;
  }
  .testimonial-text {
    font-size: 1rem;
  }
  .next-btn {
    position: absolute;
  }
}
