@import url("https://fonts.googleapis.com/css2?family=Great+Vibes&family=Montserrat&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --background-color: white;
  --text-color: black;
  --button-color: rgb(101, 9, 239);
  --textbutton-color: white;
  --header-font: "Great Vibes", cursive;
  --body-font: "Montserrat", sans-serif;
}
body {
  font-size: 62.5%;
}

.container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}

.navbar {
  height: 6rem;
  position: relative;
}

.logo h1 {
  font-size: 3rem;
  font-family: var(--header-font);
}
.logo h1 span {
  font-size: 3rem;
}
.menu-icon {
  display: none;
  padding-right: 2rem;
}
.line {
  position: absolute;
  height: 0.2rem;
  width: 2rem;
  left: 85%;
  margin-top: 1rem;
  transition: 0.5s;
  background-color: var(--text-color);
}

.line:nth-child(1) {
  top: 12px;
}
.line:nth-child(2) {
  top: 24px;
}
.line:nth-child(3) {
  top: 36px;
}
.nav-elements ul {
  display: flex;
  list-style-type: none;
  font-family: var(--body-font);
  font-weight: bolder;
}

.nav-elements a {
  font-size: 1rem;
  font-weight: bolder;
  text-decoration: none;
  color: var(--text-color);
  margin-right: 4rem;
}

.get-started a {
  background-color: var(--button-color);
  color: var(--textbutton-color);
  padding: 1rem 1rem;
  border-radius: 3rem;
}

.menu-icon .line:nth-child(2):hover {
  transform: translateX(-1rem);
  background-color: var(--button-color);
}

@media (max-width: 768px) {
  .nav-elements ul li:not(:last-child) {
    margin-right: 30px;
  }
}

@media (max-width: 1020px) {
  .menu-icon {
    display: block;
    cursor: pointer;
  }

  .nav-elements {
    position: absolute;
    right: 0;
    top: 5rem;
    background-color: rgb(220, 205, 255);
    width: 0px;
    height: calc(80vh - 10px);
    transition: all 0.3s ease-in;
    overflow: hidden;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  .nav-elements.active {
    width: 100%;
  }

  .nav-elements ul {
    display: flex;
    flex-direction: column;
  }
  .nav-elements ul li {
    margin: 3rem;
  }
  .navbar .logo h1,
  .navbar .logo span {
    font-size: 2rem;
  }
}
