@import url("https://fonts.googleapis.com/css2?family=Great+Vibes&family=Montserrat&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
:root {
  --background-color: white;
  --text-color: black;
  --button-color: #6509ef;
  --textbutton-color: white;
  --header-font: "Great Vibes", cursive;
  --body-font: "Montserrat", sans-serif;
}
body {
  font-size: 62.5%;
}
.max-width {
  width: min(320ch, 100% - 4rem);
  margin: auto;
}
.home-container {
  max-width: 900rem;
  height: 80vh;
  background-image: url("../img/home.png");
  background-size: cover;
  background-repeat: no-repeat;
}
.home-title-one,
.home-title-two {
  font-size: 4rem;
  font-family: var(--body-font);
}
.home-title-one {
  position: absolute;
  top: 40%;
}
.home-title-two {
  position: absolute;
  top: 55%;
}
.home-title-one span,
.home-title-two span {
  color: var(--button-color);
}

.home-image {
  position: absolute;
  width: 1300px;
  height: 520px;
  right: 3rem;
  bottom: 5rem;
}
/***About Container***/
.about-container {
  width: 100%;
  position: relative;
  margin-top: 4rem;
}
.about-container h1 {
  text-align: center;
  font-size: 6rem;
  font-family: var(--header-font);
  margin-bottom: 5rem;
}
span {
  color: var(--button-color);
}

.about-container-para {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.about-container-para p {
  font-size: 1.2rem;
  text-align: justify;
  font-family: var(--body-font);
  color: var(--text-color);
  font-weight: bolder;
  margin-bottom: 10rem;
}
.about-container-para span {
  font-size: 2rem;
}

/***Team Container****/
.team-container h1 {
  margin-top: 10rem;
  text-align: center;
  font-size: 4rem;
  font-family: var(--body-font);
}
.team-container h1 span {
  font-size: 6rem;
  font-family: var(--header-font);
}

/**Testimonial Container***/
.testimonial-container h1 {
  margin-top: 20rem;
  text-align: center;
  font-size: 4rem;
  font-family: var(--body-font);
}
.testimonial-container h1 span {
  font-size: 6rem;
  font-family: var(--header-font);
}

@media (max-width: 950px) {
  .home-title-one,
  .home-title-two {
    font-size: 2rem;
  }
  .about-container h1,
  .testimonial-container h1,
  .team-container h1 {
    font-size: 2rem;
  }

  .about-container h1 span,
  .team-container h1 span,
  .testimonial-container h1 span {
    font-size: 2rem;
    font-family: var(--body-font);
  }
}
